import React, { ChangeEvent, useState } from 'react';
import styles from './Form.module.css'
import { TextField } from '@mui/material';

const Forms = ({ onAddUser }: any) => {
    const [username, setUsername] = useState('');
    const [points, setPoints] = useState(0);

    const handleSubmit = (event: any) => {
        event.preventDefault();
        onAddUser({
            username,
            points,
        })
        setUsername('');
        setPoints(0);
    }

    const handleUsernameChange = ({ target }: ChangeEvent<HTMLInputElement>) => setUsername(target.value)
    const handlePointsChange = ({ target }: ChangeEvent<HTMLInputElement>) => setPoints(Number(target.value || '0'))

    return (
        <form className={styles.forms} onSubmit={handleSubmit}>
            <div>
                <TextField
                    value={username}
                    onChange={handleUsernameChange}
                    fullWidth
                    margin="dense"
                    id="username"
                    label="Usuario"
                    variant="outlined"
                    onKeyPress={(ev) => {
                        if (ev.key === 'Enter') handleSubmit(ev);
                    }} />
            </div>
            <div>
                <TextField
                    type='number'
                    value={points}
                    onChange={handlePointsChange}
                    fullWidth
                    margin="dense"
                    id="points"
                    label="Puntos"
                    variant="outlined"
                    onKeyPress={(ev) => {
                        if (ev.key === 'Enter') handleSubmit(ev);
                    }} />
            </div>
        </form>
    )
};

export default Forms;